import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

/**
 * Generated class for the ComentariosOcorrenciaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-comentarios-ocorrencia',
  templateUrl: 'comentarios-ocorrencia.html',
})
export class ComentariosOcorrenciaPage {
  params: any = {};
  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController) {
    this.params.data = navParams.get('occurence') ? navParams.get('occurence') : {};
    this.params.events ={
      'onClose': function(service: any) {
        viewCtrl.dismiss();
      },
      'onCloseOccurence': function(item: any) {
        viewCtrl.dismiss(item);
      }
    }
  }

}



